import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate"

Vue.use(Vuex);

let store=new Vuex.Store({
    plugins: [createPersistedState()],
    state:{
       val:'',
       img:'',
       list:[]
    },
    getters:{
        total (state){
            let n=0;
            state.list.forEach(k=>{
                n+=k.num*k.price
            })
            return n;
       },
       quantity(state){
            let n=0;
            state.list.forEach(k=>{
                n+=k.num
            })
            return n;
       }
    },
    mutations:{
        key(state,v){
            state.val=v
        },
        dell(state,id){
            state.val=''
        },
        gettou(state,val){
            state.img=val
        },
        add(state,k){
            state.list.push({name:k.name,price:k.specfoods[0].price,num:1})
        },
        jia(state,i){
            state.list[i].num++
        },
        jian(state,i){
            state.list[i].num--
        },
        del(state,i){
            state.list.splice(i,1)
        },
        empty(state){
            state.list=[]
        }
    },
    actions:{
        
    }
})
export default store;






