import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/Nav',
      name: 'Nav',
      component: resolve=>(require(['../components/Nav'],resolve))
    },
    {
      path: '/Homepage',
      name: 'Homepage',
      component: resolve=>(require(['../page/Homepage'],resolve)),
      // children:[
      //   {
      //     path: '/HomePage/',
      //     name: 'add',
      //     component: resolve=>(require(['../page/add'],resolve)),
      //     default:'/Homepage/add'    
      //   }
      // ]
    },
    {
      path: '/Search',
      name: 'Search',
      component: resolve=>(require(['../page/Search'],resolve))      
    },
    {
      path: '/Order',
      name: 'Order',
      component: resolve=>(require(['../page/Order'],resolve))      
    },
    {
      path: '/My',
      name: 'My',
      component: resolve=>(require(['../page/My'],resolve))      
    },
    {
      path: '/Address',
      name: 'Address',
      component: resolve=>(require(['../page/Address'],resolve))      
    },
    {
      path: '/',
      name: 'City',
      component: resolve=>(require(['../page/City'],resolve))      
    },
    {
      path: '/Signin',
      name: 'Signin',
      component: resolve=>(require(['../pages/Signin'],resolve))      
    },
    {
      path: '/Account',
      name: 'Account',
      component: resolve=>(require(['../pages/Account'],resolve))      
    },
    {
      path: '/Edit',
      name: 'Edit',
      component: resolve=>(require(['../pages/Edit'],resolve))      
    },
    {
      path: '/Added',
      name: 'Added',
      component: resolve=>(require(['../pages/Added'],resolve))      
    },
    {
      path: '/Newaddress',
      name: 'Newaddress',
      component: resolve=>(require(['../pages/Newaddress'],resolve))      
    },
    {
      path: '/aa',
      name: 'aa',
      component: resolve=>(require(['../page/aa'],resolve))      
    },
    {
      path: '/Close',
      name: 'Close',
      component: resolve=>(require(['../components/Close'],resolve))
    }
  ]
})



